/* Flex */
.flex {
  display: flex; }

.flex-column {
  display: flex;
  flex-direction: column; }

.flex-row {
  flex-direction: row; }

.space-between {
  justify-content: space-between; }

.space-around {
  justify-content: space-around; }

.align-center {
  align-items: center; }

.align-self-center {
  align-self: center; }

.align-end {
  align-items: flex-end; }

.align-self-end {
  align-self: flex-end; }

.align-start {
  align-items: flex-start; }

.align-self-start {
  align-self: flex-start; }

.justify-center {
  justify-content: center; }

.justify-start {
