.contact-list {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .contact-list .contact {
    display: flex;
    align-items: center;
    padding: 5px;
    margin: 0 0 20px 0;
    width: 300px;
    background-color: #111;
    border: 2px solid #eee;
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s; }
    .contact-list .contact:hover {
      background-color: #222; }

.contact-details {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  width: 400px;
  background-color: #111;
  border: 4px solid #eee;
  color: #eee; }

